.select {
    width: 150px;
    cursor: pointer;
    appearance: none;
    padding: 5px 30px 5px 5px;
    margin: 7px;
    background-color: #ffffff;
    color: black;
    border: 2px inset rgb(74, 199, 48);
    -moz-appearance: none;
    -webkit-appearance: none;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%234DAD4F%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'), linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-size: .65em auto, 100%;

}

.select:focus {
    outline: none;
    border: 2px solid #4bad4f;
}

.option {
    background: #73b073;
}

.option:checked {
    background: #338333;
}

.option:hover {
    box-shadow: 0 0 10px 100px #eae7e2 inset;
    transition: all .2s ease-in-out;
}



